import Button from '../pages/button.vue'
import Live from '../pages/live.vue'
import Slide from '../pages/silde.vue'
const routes = [{
    path: '/button',
    component: Button
}, {
    path: '/live',
    component: Live
}, {
    path: '/slide',
    component: Slide
}]


export default routes


